import { _decorator, Component, v3 } from 'cc';
import { Vector3 } from './Config';
const { ccclass, property } = _decorator;

@ccclass('Square')
export class Square extends Component {
    move(dir: Vector3) {
        let offset = v3(dir.x, dir.y, dir.z).multiplyScalar(4);
        this.node.position = this.node.position.add(offset);
    }
}


